import React from "react";
import hooksAndComponents from "../../utils/hooks-and-components";
import treesWaterImg from "../../assets/images/trees-water.jpg";

export default function AccordionBody(props) {
  const { InnerBlocks } = hooksAndComponents(props.isEditor);

  return (
    <div className='aahb_accordion_body'>
      <InnerBlocks
        template={[
          [
            'core/image',
            {
              url: treesWaterImg,
              alt: "aahb-trees-water"
            }
          ],
        ]}
      />
    </div>
  );
}