import React from "react";
import AccordionIcon from "./accordion-icon";
import hooksAndComponents from "../../utils/hooks-and-components";

export default function AccordionHead(props) {
  const {attributes, setAttributes, isEditor} = props;
  const { heading, subheading } = attributes;
  const { RichText } = hooksAndComponents(isEditor);

  return (
    <div className='aahb_accordion_head'>
      <AccordionIcon {...props} />
      <div className='aahb_accordion_heading'>
        <RichText
          className='aahb_heading'
          tagName='h4'
          value={heading}
          onChange={heading => setAttributes({ heading })}
          placeholder='Write heading'
        />
        <RichText
          className='aahb_subheading'
          tagName='p'
          value={subheading}
          onChange={subheading => setAttributes({ subheading })}
          placeholder='Write subheading'
        />
      </div>
    </div>
  );
}