import React from "react";
import hooksAndComponents from "../../utils/hooks-and-components";
import AccordionHead from "./accordion-head";
import AccordionBody from "./accordion-body";

export default function Output(props) {
  const { isEditor, clientId, context } = props;

  const {
    useEffect,
    useBlockProps,
    useDispatch,
    useSelect,
  } = hooksAndComponents(isEditor);

  const { getBlockRootClientId, getBlocks, getBlock } = useSelect(select => select('core/block-editor'));
  const { updateBlockAttributes } = useDispatch('core/block-editor');

  const activeItemsClientId = context?.["aahb/accordion-activeItemsClientId"];
  const parentBlock = getBlock?.(getBlockRootClientId?.(clientId));
  const siblingBlocks = getBlocks?.(parentBlock?.clientId);
  const isActive = isEditor && clientId && (clientId === activeItemsClientId);

  const activateAccordion = (targetClientId) => {
    updateBlockAttributes?.(parentBlock?.clientId, { activeItemsClientId: targetClientId });
  }

  useEffect(() => {
    const activeBlockExists = siblingBlocks?.some(block => block?.clientId === activeItemsClientId);
    if(!activeBlockExists) activateAccordion(siblingBlocks?.[0]?.clientId);
  }, [activeItemsClientId, siblingBlocks])

  return (
    <div
      {
        ...useBlockProps({ 
          className: `${isActive ? 'active' : ''}`, 
          onClick: () => activateAccordion(clientId)
        })
      }
    >
      <AccordionHead {...props} />
      <AccordionBody {...props} />
    </div>
  );
}