import React from "react";
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Tooltip as DefaultTooltip } from '@wordpress/components';
import horizontalAccordionIcon from "../../assets/icons/horizontal-accordion-icon.svg";

export default function AccordionIcon({ isEditor, attributes, setAttributes }) {
  const { iconUrl } = attributes;
  const Tooltip = isEditor ? DefaultTooltip : ({children}) => children;

  const Img = ({ onClick = () => {} }) => {
    return (
      <Tooltip text="Upload Icon">
        <div className={`aahb_accordion_icon_wrapper ${isEditor ? '' : 'frontend'}`} onClick={onClick}>
          <img className="aahb_accordion_icon" src={iconUrl || horizontalAccordionIcon} alt="Horizontal Accordion Item's Header Icon" />
            <span className="aahb_icon_upload dashicons dashicons-edit-large"></span>
        </div>
      </Tooltip>
    );
  }

  if(!isEditor) return <Img />;

  return (
    <MediaUploadCheck>
      <MediaUpload
        onSelect={(media) => setAttributes({ iconUrl: media.url })}
        allowedTypes={['image']}
        render={({ open }) => <Img onClick={open} />}
      />
    </MediaUploadCheck>
  );
}