import { useState as useDefaultState, useEffect as useDefaultEffect, useMemo as useDefaultMemo } from "react";
import { useDispatch as useDefaultDispatch, useSelect as useDefaultSelect } from '@wordpress/data';
import {
  useBlockProps as useDefaultBlockProps,
  InnerBlocks as DefaultInnerBlocks,
  RichText as DefaultRichText
} from '@wordpress/block-editor';

// Import hooks and components that can be safely used inside edit.js components
export default function hooksAndComponents(isEditor = false) {
  const useState = isEditor ? useDefaultState : () => [];
  const useEffect = isEditor ? useDefaultEffect : () => {};
  const useDispatch = isEditor ? useDefaultDispatch : () => ({});
  const useSelect = isEditor ? useDefaultSelect : () => ({});
  const useMemo = isEditor ? useDefaultMemo : () => {};

  const useBlockProps = isEditor ? useDefaultBlockProps : useDefaultBlockProps.save;
  const InnerBlocks = isEditor ? DefaultInnerBlocks : DefaultInnerBlocks.Content;
  const RichText = isEditor ? DefaultRichText : DefaultRichText.Content;

  return {
    useState,
    useEffect,
    useMemo,
    useBlockProps,
    InnerBlocks,
    RichText,
    useDispatch,
    useSelect,
  };
}