import React, { useEffect } from "react";
import {__} from '@wordpress/i18n';

import Output from "./output";
import Editor from "./editor";
import './editor.scss';

const Edit = (props) => {
    const {setAttributes, context} = props;

    return (
        <>
            <Editor.Settings {...props} />
            <Editor.Styles {...props} />
            <Output isEditor {...props} />
        </>
    );
};

export default Edit;